.header {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  margin-top: 30px;
  margin-bottom: 80px;
  padding: 25px 20px;
}

.header__logo {
  margin: 0;
  margin-right: 6.6%;
  font-size: 50px;
  color: #ffffff;
  text-decoration: none;
}

.header__logo:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.header__list {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.header__list-item {
  margin-right: 2.5%;
}

.header__link {
  font-weight: bold;
  color: var(--green);
  text-decoration: none;
}

.header__link:hover {
  text-decoration: underline;
}
.random-planet {
  display: -webkit-flex;
  display: flex;
  margin-bottom: 80px;
  padding: 20px;
  min-height: calc(40px + 250px + 2px); /* padding image border */
}

.random-planet__img {
  width: 250px;
  height: 250px;
  border-radius: 5px;
  margin-right: 5%;
}

.random-planet__list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.random-planet__list-item {
  padding: 10px;
  border-top: 1px solid var(--border-color);
}

@keyframes lds-rolling {
  0% {
    -webkit-transform: translate(-50%, -50%) rotate(0deg);
    transform: translate(-50%, -50%) rotate(0deg);
  }
  100% {
    -webkit-transform: translate(-50%, -50%) rotate(360deg);
    transform: translate(-50%, -50%) rotate(360deg);
  }
}
@-webkit-keyframes lds-rolling {
  0% {
    -webkit-transform: translate(-50%, -50%) rotate(0deg);
    transform: translate(-50%, -50%) rotate(0deg);
  }
  100% {
    -webkit-transform: translate(-50%, -50%) rotate(360deg);
    transform: translate(-50%, -50%) rotate(360deg);
  }
}

.spinner {
  -webkit-align-self: center;
          align-self: center;
  margin: 0 auto;
}

.lds-rolling {
  position: relative;
}
.lds-rolling div,
.lds-rolling div:after {
  position: absolute;
  width: 160px;
  height: 160px;
  border: 20px solid #00bc8c;
  border-top-color: transparent;
  border-radius: 50%;
}
.lds-rolling div {
  -webkit-animation: lds-rolling 1s linear infinite;
  animation: lds-rolling 1s linear infinite;
  top: 100px;
  left: 100px;
}
.lds-rolling div:after {
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
}
.lds-rolling {
  width: 100px;
  height: 100px;
  -webkit-transform: translate(-50px, -50px) scale(0.5) translate(50px, 50px);
  transform: translate(-50px, -50px) scale(0.5) translate(50px, 50px);
}
.error-indicator {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  color: #c78f22;
}

.error-indicator img {
  margin-bottom: 1rem;
}

.error-indicator .boom {
  font-size: 1.7rem;
}
.row {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin-bottom: 80px;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.row__left {
  width: 48%;
}

.row__right {
  display: -webkit-flex;
  display: flex;
  width: 48%;
}
.item-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.item-list__item {
  border-bottom: 1px solid var(--border-color);
}

.item-list__item:last-child {
  border-bottom: none; 
}

.item-list__btn {
  width: 100%;
  height: 100%;
  padding: 20px;
}
.item-details {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  -webkit-align-self: flex-start;
          align-self: flex-start;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  width: 100%;
  min-width: 230px;
  padding: 20px;
}

.item-details__imb-wrap {

  margin-right: 5%;
}

.item-details__img {
  width: 230px;
  height: auto;
  object-fit: contain;
  object-position: top left;
  border-radius: 5px;
}

.item-details__block {
  -webkit-flex-grow: 1;
          flex-grow: 1;
}

.item-details__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.item-details__list-item {
  padding: 10px;
  border-top: 1px solid var(--border-color); 
}


*, *::before, *::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
}

:root {
  --dark-gray: #222222;
  --gray: #444444;
  --green: #00bc8c;
  --light-gray: #303030;
  --border-color: var(--gray);
}

body {
  margin: 0;
  min-width: 320px;
  font-size: 18px;
  font-family: "Arial", sans-serif;
  color: #ffffff;
  background-color: #222222;
  background-color: var(--dark-gray);
}

.box {
  border: 1px solid #444444;
  border: 1px solid var(--border-color);
  background-color: #303030;
  background-color: var(--light-gray);
  border-radius: 5px;
}

.btn {
  border: none;
  background-color: transparent;
  color: #ffffff;
  font-size: inherit;
  text-align: left;
  cursor: pointer;
}

.btn:hover,
.btn:focus {
  outline: 1px solid #00bc8c;
  outline: 1px solid var(--green);
}

@media  (min-width: 1550px) {
  .container {
    width: 1550px;
    margin: 0 auto;
  }
}
